import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatSortModule } from '@angular/material/sort';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { RouterModule } from '@angular/router';
import { RolePermissionService } from '../../@services/role-permission.service';
import { RolePermissionDialogComponent } from './role-permission-dialog/role-permission-dialog.component';
import { RolePermissionComponent } from './role-permission.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatTableModule, MatButtonModule, MatChipsModule, MatIconModule,
  MatPaginatorModule, MatSortModule, MatDialogModule, MatSelectModule, MatInputModule];
const TEI_MATERIAL_MODULES = [];

@NgModule({
  declarations: [RolePermissionComponent, RolePermissionDialogComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: RolePermissionComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [{ provide: 'IUMRolePermissionService', useClass: RolePermissionService }],
  exports: [RolePermissionComponent]
})
export class RolePermissionModule { }
