import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { SnackBarService } from '@tei/material';
import { Permission, PostRolePermissionRequest } from '../../../@intermediate/role-permission';
import { RolePermissionService } from '../../../@services/role-permission.service';

@Component({
  selector: 'tei-user-management-role-permission-dialog',
  templateUrl: './role-permission-dialog.component.html',
  styleUrls: ['./role-permission-dialog.component.css']
})
export class RolePermissionDialogComponent implements OnInit
{
  model: PostRolePermissionRequest = { RoleID: null, RoleName: '', PermissionIDs: [] };
  permissionIDs: [];
  permissions: Permission[];

  constructor(
    public dialogRef: MatDialogRef<RolePermissionDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public rolePermissionService: RolePermissionService,
    private snackbarService: SnackBarService
  )
  {
    if (data)
    {
      if (data.role)
      {
        this.model = {
          RoleID: data.role.RoleID,
          RoleName: data.role.RoleName,
          PermissionIDs: data.role.Permissions
        };
      }

      this.permissions = data.permissions;
      this.permissionIDs = data.role.Permissions.map(pr => pr.PermissionID);
    }
  }

  onDiscard(): void
  {
    this.dialogRef.close();
  };

  ngOnInit(): void
  {
  };

  canSave()
  {
    if (!this.permissionIDs || this.permissionIDs.length == 0)
    {
      return false;
    }

    return true;
  }

  onSave()
  {
    const newRolePermission: PostRolePermissionRequest = { RoleID: this.model.RoleID, RoleName: this.model.RoleName, PermissionIDs: [] };

    this.permissionIDs.forEach(pId =>
    {
      const found = this.model.PermissionIDs.find(p => p.PermissionID == pId);

      if (found)
      {
        newRolePermission.PermissionIDs.push({ PermissionID: pId, State: 0 });
      }
      else
      {
        newRolePermission.PermissionIDs.push({ PermissionID: pId, State: 1 });
      }
    });

    this.model.PermissionIDs.forEach(permission =>
    {
      const found = newRolePermission.PermissionIDs.find(p => p.PermissionID == permission.PermissionID);

      if (!found)
      {
        newRolePermission.PermissionIDs.push({ PermissionID: permission.PermissionID, State: -1 });
      }
    });

    newRolePermission.PermissionIDs = newRolePermission.PermissionIDs.filter(r => r.State != 0);

    this.rolePermissionService.Post(newRolePermission).subscribe(
      () =>
      {
        this.snackbarService.showSuccess('Permissions Updated Successfully.');
        this.onDiscard();
      },
      (err: any) =>
      {
        let message: string = err.error;

        this.snackbarService.showError(message);
      });
  }
}
