import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatSort } from '@angular/material/sort';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { DeleteRolePermissionRequest, GetRolePermissionRequest, GetRolePermissionResponse, IUMRolePermissionService, PostRolePermissionRequestForm, RolePermission, RolePermissionMetadata } from '../../@intermediate/role-permission';
import { RolePermissionService } from '../../@services/role-permission.service';
import { RolePermissionDialogComponent } from './role-permission-dialog/role-permission-dialog.component';

@Component({
  selector: 'tei-user-management-pages-role-permission',
  templateUrl: './role-permission.component.html',
  styleUrls: ['./role-permission.component.scss']
})
export class RolePermissionComponent implements OnInit
{
  model: GetRolePermissionResponse;
  form: PostRolePermissionRequestForm;

  metadata = new RolePermissionMetadata();
  datasource: MatTableDataSource<RolePermission>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(@Inject('IUMRolePermissionService') private RolePermissionService: IUMRolePermissionService,
    private confirmdialogService: ConfirmDialogService, private snackbarService: SnackBarService, public dialog: MatDialog,
    private rolePermissionService: RolePermissionService) { }

  ngOnInit()
  {
    this.form = new PostRolePermissionRequestForm();

    this.reload();
  }

  private load(request: GetRolePermissionRequest)
  {
    if (!this.model)
    {
      this.model = new GetRolePermissionResponse();
    }

    this.RolePermissionService.Get(request).subscribe(response =>
    {
      this.model.RolePermissions = response.RolePermissions;
      this.model.Permissions = response.Permissions;
      this.bindRolePermissions(this.model.RolePermissions);
    }, err => console.log('err', err));
  }

  reload()
  {
    const request = new GetRolePermissionRequest();

    this.load(request);
  }

  save(form: PostRolePermissionRequestForm)
  {
    this.RolePermissionService.Post(form.value).subscribe(response =>
    {

    });
  }

  private bindRolePermissions(rolePermissions: RolePermission[])
  {
    this.datasource = new MatTableDataSource<RolePermission>(rolePermissions);
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }

  openDialog(role: RolePermission): void
  {
    if (!role)
    {
      role = new RolePermission();
    }

    const dialogRef = this.dialog.open(RolePermissionDialogComponent, {
      data: { role, permissions: this.model.Permissions },
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      this.load(new GetRolePermissionRequest());
    });
  }

  openDeleteDialog(roleID: number, permissionID: number)
  {
    const dialogData: DialogData = {
      title: 'Delete',
      message: `Are you sure you want to delete this permission?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        const request = new DeleteRolePermissionRequest();
        request.RoleID = roleID;
        request.PermissionID = permissionID;
        this.rolePermissionService.Delete(request).subscribe(response =>
        {
          this.snackbarService.showSuccess('Permission Deleted Ssuccessfully.');
          this.load(new GetRolePermissionRequest());
        });
      }
    });
  }
}
