import { AbstractControl, UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { GetCreateuserResponse } from './get-createuser.model';

export class PostCreateuserRequest
{
  public FirstName: string;
  public LastName: string;
  public Email: string;
  public Username: string;
  public Phone: string;
  public Password: string;
  public ConfirmPassword: string;
  public Roles: string[];
}

export class PostCreateuserRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      FirstName: new UntypedFormControl('', Validators.required),
      LastName: new UntypedFormControl('', Validators.required),
      Email: new UntypedFormControl('', [Validators.required, Validators.pattern(/^(\d{10}|\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3}))$/)]),
      Username: new UntypedFormControl(''),
      Phone: new UntypedFormControl('', [Validators.pattern(/^\(\d{3}\)\s\d{3}-\d{4}$/), Validators.required]),
      PasswordGroup: new UntypedFormGroup({
      Password: new UntypedFormControl('',[Validators.required,Validators.minLength(6)]),
      ConfirmPassword: new UntypedFormControl('', [Validators.required, Validators.minLength(6)]),
      }, { validators: matchpassword }),
      Roles: new UntypedFormControl()
    });
  }

  public value: PostCreateuserRequest;

  public setValue(value: GetCreateuserResponse)
  {
    const request = new PostCreateuserRequest();

    //request.Title = value.Title;



    super.setValue(request);
  }
}

export class PostCreateuserResponse
{
  ExistingUser:boolean;
}

function matchpassword(group: AbstractControl): { [key: string]: any; } | null
{
  const passwordControl = group.get('Password');
  const confirmpasswordControl = group.get('ConfirmPassword');

  if (confirmpasswordControl.errors && !confirmpasswordControl.errors.passwordMismatch)
  {
    return null;
  }

  if (passwordControl.value === confirmpasswordControl.value || confirmpasswordControl.pristine)
  {
    //return null;
    confirmpasswordControl.setErrors(null);
  } else
  {
    //return { 'passwordMismatch': true };
    confirmpasswordControl.setErrors({ 'passwordMismatch': true });
  }
}
