import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PermissionsComponent } from '@tei/admin-admin-center';
import { RouteData } from '@tei/core';
import { CreateuserComponent } from './pages/createuser/createuser.component';
import { GroupStaffComponent } from './pages/group-staff/group-staff.component';
import { PagesComponent } from './pages/pages.component';
import { RolesWrapperComponent } from './pages/roles-wrapper/roles-wrapper.component';
import { SettingsComponent } from './pages/settings/settings.component';
import { UserComponent } from './pages/user/user.component';

const routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      {
        path: 'staff',
        component: GroupStaffComponent,
        data: { RouteData: new RouteData({ Title: 'Staff' }) }
      },
      {
        path: 'settings',
        component: SettingsComponent,
        data: { RouteData: new RouteData({ Title: 'Settings' }) }
      },
      {
        path: '',
        component: UserComponent,
        data: { RouteData: new RouteData({ Title: 'User' }) }
      },
      {
        path: 'create-user',
        component: CreateuserComponent,
        data: { RouteData: new RouteData({ Title: 'Create User', CanGoBack: true, BackRoute: '/' }) }
      },
      {
        path: 'create-user/:userName',
        component: CreateuserComponent,
        data: { RouteData: new RouteData({ Title: 'Create User', CanGoBack: true, BackRoute: '/' }) }
      },
      {
        path: 'roles/:id/permissions',
        component: PermissionsComponent
      },
      {
        path: 'roles',
        component: RolesWrapperComponent
      },
    ],
  },
  { path: '**', redirectTo: '' },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class UserManagementRoutingModule
{
}
