import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { ContactAvatarModule } from '@tei/office-contacts';
import { GroupAdminStaffService } from '../../@services/group-admin-staff.service';
import { TreeModule } from '../../components/tree/tree.module';
import { AccessPortalUserComponent } from './access-portal-user/access-portal-user.component';
import { AccountUserComponent } from './account-user/account-user.component';
import { EditUserComponent } from './edit-user/edit-user.component';
import { GroupStaffComponent } from './group-staff.component';
import { InviteUsersComponent } from './invite-users/invite-users.component';


const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatIconModule, MatButtonModule, MatDividerModule, MatMenuModule, MatCardModule, MatTabsModule, MatTableModule];
const TEI_MATERIAL_MODULES = [QuickSearchFilterChipModule, ContactAvatarModule, LoadingModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TeiDateTranslateModule];
const USER_MANAGEMENT_MODULES = [TreeModule];
const COMPONENTS = [GroupStaffComponent, InviteUsersComponent, EditUserComponent, AccessPortalUserComponent, AccountUserComponent];


@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...USER_MANAGEMENT_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...USER_MANAGEMENT_MODULES,
    ...TEI_CORE_MODULES,
    ...COMPONENTS
  ],
  providers: [
    { provide: 'IGroupAdminStaffService', useClass: GroupAdminStaffService }
  ],
})
export class GroupStaffModule { }
