import { UntypedFormGroup } from '@angular/forms';
import { GetUserResponse } from './get-user.model';

export class PostUserRequest
{
  public UserID: number;
  public IsActive: boolean;
}

export class PostUserRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({});
  }

  public value: PostUserRequest;

  public setValue(value: GetUserResponse)
  {
    const request = new PostUserRequest();

    super.setValue(request);
  }
}

