import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { Component, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup } from '@angular/forms';
import { MatLegacyChipInputEvent as MatChipInputEvent } from '@angular/material/legacy-chips';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ModelDrivenFormGroupControlValueAccessor } from '@tei/core';
import { ItemNode } from '../../../components/tree/tree.model';


@Component({
  selector: 'tei-admin-admin-center-invite-users',
  templateUrl: './invite-users.component.html',
  styleUrls: ['./invite-users.component.scss']
})
export class InviteUsersComponent extends ModelDrivenFormGroupControlValueAccessor implements OnInit
{
  treeData: Array<ItemNode> = [
    {
      Item: { Title: 'Silver Coast Group', Id: '1', Key: '2' },
      Children: [
        {
          Item: { Title: 'Silver Coast Club', Id: '2', Key: '2' },
          Children: []
        },
        {
          Item: { Title: 'Silver Coast Play', Id: '3', Key: '3' },
          Children: [
            {
              Item: { Title: 'Coastal Lounge', Id: '4', Key: '4' },
              Children: []
            },
            {
              Item: { Title: 'Islander Oyster Bar', Id: '5', Key: '5' },
              Children: []
            },
            {
              Item: { Title: 'Pro Shop', Id: '6', Key: '6' },
              Children: []
            }
          ]
        }
      ]
    }
  ];
  form: UntypedFormGroup;
  emails: string[] = [];
  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  selectable = true;
  removable = true;
  constructor(private formBuilder: UntypedFormBuilder, @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    private inviteUserDialogRef: MatDialogRef<InviteUsersComponent>)
  {
    super(_errorStateMatcher);
  }

  ngOnInit(): void
  {
    this.form = this.formBuilder.group({
      emails: [this.emails],
      email: ['']
    });
  }

  get formControls()
  {
    return this.form.controls;
  }

  inviteUser(): void
  {

  }

  addEmailChip(event: MatChipInputEvent): void
  {
    const value = (event.value || '').trim();
    if (value)
    {
      this.emails.push(value);
    }
    this.formControls.email.setValue('');
  }

  removeEmailChip(email: string): void
  {
    const index = this.emails.indexOf(email);
    if (index >= 0)
    {
      this.emails.splice(index, 1);
    }
  }

  get sendEmailInviteText(): string
  {
    return this.emails.length > 0 ? 'USER_MANAGEMENT.GROUP_STAFF.INVITE_USERS.SEND_INVITE' : 'USER_MANAGEMENT.GROUP_STAFF.INVITE_USERS.EMAIL_REQUIRED';
  }

  close(): void
  {
    this.inviteUserDialogRef.close();
  }
}
