import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { SnackBarService } from '@tei/material';
import { Permission } from '../../../@intermediate/role-permission/models/role-permission.model';
import { Designation } from '../../../@intermediate/role/models/get-role.model';
import { PostRoleRequest } from '../../../@intermediate/role/models/post-role.model';
import { RoleService } from '../../../@services/role.service';

@Component({
  selector: 'tei-user-management-role-dialog',
  templateUrl: './role-dialog.component.html',
  styleUrls: ['./role-dialog.component.css']
})
export class RoleDialogComponent implements OnInit
{
  model: PostRoleRequest = { Designation: null, RoleID: null, RoleName: '', PermissionIDs: [] };
  roleName: string = '';
  designations: Designation[];
  permissionIDs: [];
  permissions: Permission[];
  SelectedDesignationID: number;

  constructor(
    public dialogRef: MatDialogRef<RoleDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public roleService: RoleService,
    private snackbarService: SnackBarService

  )
  {
    this.designations = data?.designations;
    this.permissions = data?.permissions;

    if (data?.role.RoleID)
    {
      this.model = data;
      this.roleName = data.role.RoleName;
    } else
    {

    }
  }

  onDiscard(): void
  {
    this.dialogRef.close();
  }

  ngOnInit(): void
  {
  }

  canSave()
  {
    if (this.roleName.trim() === '' || this.roleName === this.model.RoleName)
    {
      return false;
    }

    if (!this.SelectedDesignationID)
    {
      return false;
    }

    if (!this.permissionIDs || this.permissionIDs.length == 0)
    {
      return false;
    }


    return true;
  }

  onSave()
  {
    const newRole: PostRoleRequest = {
      RoleID: this.model.RoleID,
      RoleName: this.roleName,
      Designation: this.designations[this.SelectedDesignationID - 1],
      PermissionIDs: []
    };

    this.permissionIDs.forEach(permission =>
    {
      newRole.PermissionIDs.push({ PermissionID: permission, State: 1 });
    });

    this.roleService.Submit(newRole).subscribe(
      () =>
      {
        this.snackbarService.showSuccess('Role saved.');
        this.onDiscard();
      },
      (err: any) =>
      {
        let message: string = err.error;

        this.snackbarService.showError(message);
      });
  }
}
