import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Inject, OnInit, Pipe, PipeTransform, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatSort } from '@angular/material/sort';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { MatLegacyTabChangeEvent as MatTabChangeEvent } from '@angular/material/legacy-tabs';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { DeleteRoleRequest, UpdateRoleRequest } from '../../@intermediate/role';
import { Permission } from '../../@intermediate/role-permission/models/role-permission.model';
import { Designation, GetRoleRequest, RoleDetail } from '../../@intermediate/role/models/get-role.model';
import { GetUserRequest, GetUserResponse, IUserService, PostUserRequest, PostUserRequestForm, UserDetails, UserDetailsMetadata } from '../../@intermediate/user';
import { UserResetPasswordRequest } from '../../@intermediate/user/models/user-reset-password.model';
import { RoleService } from '../../@services/role.service';
import { RolePermissionComponent } from '../role-permission/role-permission.component';
import { DeleteRoleDialogComponent } from './delete-role-dialog/delete-role-dialog.component';
import { RoleDialogComponent } from './role-dialog/role-dialog.component';
import { UserRoleDialogComponent } from './user-role-dialog/user-role-dialog.component';

@Component({
  selector: 'user-management-pages-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class UserComponent implements OnInit
{
  model: GetUserResponse;
  form: PostUserRequestForm;
  metadata = new UserDetailsMetadata();
  datasource: MatTableDataSource<UserDetails>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @ViewChild(RolePermissionComponent) RolePermissionComponent: RolePermissionComponent;

  roles: Designation[] = [];
  permissions: Permission[] = [];
  storedRoles: Designation[];
  roleDetails: RoleDetail[] = [];
  selectedRole: RoleDetail = null;
  updateRoleRequest: UpdateRoleRequest = { RoleDetails: [], Roles: [] };



  constructor(@Inject('IUserService') private UserService: IUserService, private router: Router,
    private confirmdialogService: ConfirmDialogService,
    private snackbarService: SnackBarService,
    public dialog: MatDialog,
    private roleService: RoleService)
  {

  }

  ngOnInit()
  {
    this.fillData();
  }

  fillData()
  {
    const request = new GetUserRequest();
    this.load(request);
  }

  applyFilter(event: Event)
  {
    const filterValue = (event.target as HTMLInputElement).value;
    this.datasource.filter = filterValue.trim().toLowerCase();
  }

  private load(request: GetUserRequest)
  {
    if (!this.model)
    {
      this.model = new GetUserResponse();
    }

    this.UserService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.bindUsers(this.model.UserDetails);
    });

    this.loadRoles();
  }

  private loadRoles(reorder?: boolean)
  {
    const roleRequest: GetRoleRequest = {};


    this.roleService.Get(roleRequest).subscribe(response =>
    {
      this.roles = response.Designations.map(x => Object.assign({}, x));;
      this.permissions = response.Permissions.map(x => Object.assign({}, x));;
      this.storedRoles = response.Designations.map(x => Object.assign({}, x));
      this.updateRoleRequest.Roles = [];
      this.roleDetails = response.RoleDetails;
      this.RolePermissionComponent?.reload();

      if (reorder)
      {
        this.saveOrderChanges(false);
      }
    },
      err =>
      {
        let message: string = err.error;

        this.snackbarService.showError(message);
      });
  }

  changeActivation(userID, isActive)
  {
    const postUserRequest: PostUserRequest = { UserID: userID, IsActive: !isActive };

    this.UserService.Post(postUserRequest).subscribe(response =>
    {
      this.load(new GetUserRequest());
      this.snackbarService.showSuccess('User Successfully Updated.');
    });
  }

  private bindUsers(userdetails: UserDetails[])
  {
    this.datasource = new MatTableDataSource<UserDetails>(userdetails);
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }

  getDesignationName(designationID: number)
  {
    const role = this.roles.find(r => r.DesignationID == designationID);
    return role ? role.Name : 'No Designation';
  }

  setSelectedRole(RoleID: number)
  {
    this.selectedRole = this.roleDetails.find(r => r.RoleID == RoleID);
  }

  DeleteItem(userID: number)
  {
    const dialogData: DialogData = {
      title: 'Delete',
      message: `Are you sure you want to delete this user?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.deleteItem(userID);
      }
    });
  }

  private deleteItem(userID: number)
  {
    const request = new PostUserRequest();
    request.UserID = userID;
    this.UserService.Post(request).subscribe(response =>
    {
      this.fillData();
      this.snackbarService.showSuccess('User is deleted');
    });
  }

  openDialog(role): void
  {
    const latestRole = this.roles[this.roles.length - 1];

    let data: any = {};

    data.designations = this.roles;
    data.permissions = this.permissions;

    if (!role)
    {
      data.role = latestRole ? latestRole.Priority : 1;
    }

    const dialogRef = this.dialog.open(RoleDialogComponent, {
      data,
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      this.loadRoles();
    });
  }

  openDeleteDialog(roleID: number): void
  {
    const data: DeleteRoleRequest = { RoleID: roleID };
    const dialogRef = this.dialog.open(DeleteRoleDialogComponent, {
      data
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      this.loadRoles(true);
    });
  }

  onSave(showMessage: boolean = true)
  {
    this.roleService.Update(this.updateRoleRequest).subscribe(
      () =>
      {
        if (showMessage)
          this.snackbarService.showSuccess('Changes saved.');
        this.updateRoleRequest.RoleDetails = [];
        this.loadRoles();
      },
      err =>
      {
        let message: string = err.error;

        this.snackbarService.showError(message);
      });
  }

  onSelect(dnnRoleID, roleDetailId, roleId)
  {
    const existing = this.updateRoleRequest.RoleDetails.find(i => i.DNNRoleID == dnnRoleID);

    if (existing)
    {
      this.updateRoleRequest.RoleDetails[this.updateRoleRequest.RoleDetails.indexOf(existing)] = {
        DNNRoleID: dnnRoleID, RoleDetailID: roleDetailId, RoleID: roleId
      };
    }
    else
    {
      this.updateRoleRequest.RoleDetails.push({ DNNRoleID: dnnRoleID, RoleDetailID: roleDetailId, RoleID: roleId });
    }
  }

  getSelectedTab()
  {
    const tabIndex = localStorage.getItem('selected_tab');
    return tabIndex || 0;
  }

  onTabChange(event: MatTabChangeEvent)
  {
    localStorage.setItem('selected_tab', event.index.toString());
  }

  drop(event: CdkDragDrop<string[]>)
  {
    moveItemInArray(this.roles, event.previousIndex, event.currentIndex);
    this.saveOrderChanges(true);
  }

  saveOrderChanges(showMessage)
  {
    this.roles.forEach((role, index) =>
    {
      role.Priority = index + 1;
    });

    this.updateRoleRequest.Roles = [];

    this.storedRoles.forEach(role =>
    {
      const found = this.roles.find(r => r.DesignationID === role.DesignationID);

      if (found.Priority !== role.Priority)
      {
        //this.updateRoleRequest.Roles.push(found);
      }
    });

    if (this.updateRoleRequest.Roles.length)
      this.onSave(showMessage);
  }

  onSecurityRoles(userName: string)
  {
    const userDetail = this.model.UserDetails.find(u => u.Username == userName);

    let data: any = {
      userDetail: userDetail,
      roles: this.roleDetails
    };

    const dialogRef = this.dialog.open(UserRoleDialogComponent, {
      width: '33rem',
      data,
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      userDetail.Roles = result.NewUserRoles;
    });
  }

  resetPasswordDialogConfirmation(userName: string)
  {
    const dialogData: DialogData = {
      title: 'Reset Password',
      message: `Are you sure you want to send reset password Email to this user?`,
      textOkayButton: 'Send',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.onResetPasswordSend(userName);
      }
    });
  }

  onResetPasswordSend(userName: string)
  {
    const userDetail = this.model.UserDetails.find(u => u.Username == userName);

    const authenticationType = userDetail.DesignationGroupIdentifier;
    let redirectUrl;
    switch (authenticationType)
    {
      case 'Public':
        redirectUrl = window.location.origin + '/auth/public-reset-password';
        break;
      case 'Member':
        redirectUrl = window.location.origin + '/auth/member-reset-password';
        break;
      case 'Staff':
        redirectUrl = window.location.origin + '/auth/staff-reset-password';
        break;
    }

    const request: UserResetPasswordRequest = {
      RedirectUrl: redirectUrl,
      UserName: userName,
      AdditionalParameters: { AuthenticationType: authenticationType },
      //TODO bring this to gateway
      TemplateKeyValues: null
    };

    this.UserService.UserResetPassword(request).subscribe(() =>
    {
      this.snackbarService.showSuccess('A reset password link has been sent to the email.');
    }, (error) =>
    {
      this.snackbarService.showSuccess('An error has occured.');
    });
  }

  onImpersonateUser(userUID: string)
  {
    this.router.navigate(
      ['auth/handle-redirection'],
      {
        queryParams:
        {
          impersonatedUserUID: userUID,
          refresh: 'true'
        }
      }
    );
  }


}

@Pipe({ name: 'safeHtml' })
export class Safe implements PipeTransform
{
  constructor(private sanitizer: DomSanitizer) { }

  transform(style: string)
  {
    style = style.replace(/&lt;/g, '<').replace(/&gt;/g, '>');
    return this.sanitizer.bypassSecurityTrustHtml(style);
    //return this.sanitizer.bypassSecurityTrustStyle(style);
    // return this.sanitizer.bypassSecurityTrustXxx(style); - see docs
  }

}
