import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { PermissionsModule } from '@tei/admin-admin-center';
import { ConfirmDialogModule, SnackBarModule } from '@tei/material';
import { SettingsCardLayoutModule } from '@tei/theme';
import { CreateuserModule } from './pages/createuser/createuser.module';
import { GroupStaffModule } from './pages/group-staff/group-staff.module';
import { PagesModule } from './pages/pages.module';
import { RolesWrapperModule } from './pages/roles-wrapper/roles-wrapper.module';
import { SettingsModule } from './pages/settings/settings.module';
import { UserModule } from './pages/user/user.module';
