import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IGroupAdminStaffService } from '../@intermediate/group-staff/group-admin-staff.interface';
import { GetGroupAdminStaffResponse } from '../@intermediate/group-staff/models/get-group-admin-staff.model';


@Injectable({
  providedIn: 'root'
})
export class GroupAdminStaffService implements IGroupAdminStaffService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Security/UserManagement/Staff';
  }

  Get(): Observable<GetGroupAdminStaffResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetGroupAdminStaffResponse>(apiUrl);
  }
}

