import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { SnackBarService } from '@tei/material';
import { PostUserRoleRequest, Role } from '../../../@intermediate/user/models/post-user-role.model';
import { UserService } from '../../../@services/user.service';

@Component({
  selector: 'tei-user-management-user-role-dialog',
  templateUrl: './user-role-dialog.component.html',
  styleUrls: ['./user-role-dialog.component.css']
})
export class UserRoleDialogComponent implements OnInit
{
  model: PostUserRoleRequest;
  roleIDs: number[];
  tempRoleIDs: number[];
  roles: Role[];
  designationType: string;


  constructor(
    public dialogRef: MatDialogRef<UserRoleDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public userService: UserService,
    private snackbarService: SnackBarService
  )
  {
    if (data)
    {
      if (data.userDetail.Roles)
      {
        this.designationType = data.userDetail.DesignationGroupIdentifier;

        this.model = {
          Username: data.userDetail.Username,
          Roles: data.userDetail.Roles
        };

        this.roleIDs = data.userDetail.Roles.map(r => r.RoleID);
        this.tempRoleIDs = this.roleIDs;

        if (data.userDetail.DesignationGroupIdentifier === "Administrator")
        {
          this.roles = data.roles.map(r => ({ RoleName: r.RoleName, RoleID: r.RoleID }));
        } else
        {
          this.roles = data.roles.filter(r => r.DesignationID === data.userDetail.DesignationGroupID).map(r => ({ RoleName: r.RoleName, RoleID: r.RoleID }));
        }

      }
    }
  }

  onDiscard(): void
  {
    this.dialogRef.close();
  };

  ngOnInit(): void
  {
  };

  onSave()
  {
    if (!this.validation())
    {
      this.snackbarService.showInfo("At least one role should be selected");
      return;
    }

    let request = new PostUserRoleRequest();
    request.Username = this.model.Username;

    request.Roles = this.roles.filter(r => this.roleIDs.indexOf(r.RoleID) >= 0).map(r => ({ RoleName: r.RoleName, RoleID: r.RoleID }));

    this.userService.PostUserRole(request).subscribe(
      (result) =>
      {
        this.dialogRef.close(result);
        this.snackbarService.showSuccess('Roles Are Updated Successfully.');
      }
    );
  }

  validation()
  {
    if (this.roleIDs.length > 0)
    {
      return true;
    }
    else
    {
      return false;
    }
  }

  onDopdownOpen()
  {
    this.tempRoleIDs = this.roleIDs;
  }

  applyRoleIDs()
  {
    this.roleIDs = this.tempRoleIDs;
  }

}
