import { Component, OnInit, Inject } from '@angular/core';
import { ISettingsService, GetSettingsRequest, GetSettingsResponse, PostSettingsRequest, PostSettingsRequestForm, PostSettingsResponse } from '../../@intermediate/settings';

@Component({
  selector: 'tei-user-management-pages-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit
{
  model: GetSettingsResponse;
  form: PostSettingsRequestForm;

  constructor(@Inject('ISettingsService') private settingsService: ISettingsService)
  {

  }

  ngOnInit()
  {
    this.form = new PostSettingsRequestForm();
    const request = new GetSettingsRequest();

    this.load(request);
  }

  private load(request: GetSettingsRequest)
  {
    if (!this.model)
    {
      this.model = new GetSettingsResponse();
    }

    this.settingsService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.form.setValue(response);
    });
  }

  save(form: PostSettingsRequestForm)
  {
    this.settingsService.Post(form.value).subscribe(response =>
    {

    });
  }
}
