import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CreateuserComponent } from './createuser.component';
import { CreateuserService } from '../../@services/createuser.service';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatSortModule } from '@angular/material/sort';
import { PhoneMaskDirective } from './phone-mask.directive';
import { SnackBarModule } from '@tei/material';

const BASE_MODULES = [CommonModule,FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule, MatDividerModule, MatInputModule, MatTableModule, MatButtonModule, MatCheckboxModule, MatPaginatorModule, MatSortModule];
const TEI_MATERIAL_MODULES = [SnackBarModule];

@NgModule({
  declarations: [CreateuserComponent, PhoneMaskDirective],
  imports: [
    RouterModule.forChild([{ path: '', component: CreateuserComponent }]),
    ...BASE_MODULES,
	...MATERIAL_MODULES,
	...TEI_MATERIAL_MODULES
  ],
  providers:[{provide: 'ICreateuserService', useClass: CreateuserService}],
})
export class CreateuserModule { }
