import { Component, OnInit, Inject } from '@angular/core';
import { IDefaultService, GetDefaultRequest, GetDefaultResponse } from '../../@intermediate/default';

@Component({
  selector: 'tei-user-management-pages-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent implements OnInit
{
  model: GetDefaultResponse;

  constructor(@Inject('IDefaultService') private defaultService: IDefaultService)
  {

  }

  ngOnInit()
  {
    const request = new GetDefaultRequest();
    this.load(request);
  }

  private load(request: GetDefaultRequest)
  {
    if (!this.model)
    {
      this.model = new GetDefaultResponse();
    }

    this.defaultService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }
}
