import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetCreateuserRequest, GetCreateuserResponse, ICreateuserService, PostCreateuserRequest, PostCreateuserResponse } from '../@intermediate/createuser';

@Injectable({
  providedIn: 'root'
})

export class CreateuserService implements ICreateuserService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Security/UserManagement/Createuser';
  }

  public Get(request: GetCreateuserRequest): Observable<GetCreateuserResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetCreateuserResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostCreateuserRequest): Observable<PostCreateuserResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostCreateuserResponse>(apiUrl, request);
  }
}
