import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeleteRoleRequest, DeleteRoleResponse, GetRoleRequest, GetRoleResponse, IRoleService, UpdateRoleRequest, UpdateRoleResponse } from '../@intermediate/role';
import { PostRoleRequest, PostRoleResponse } from '../@intermediate/role/models/post-role.model';

@Injectable({
  providedIn: 'root'
})

export class RoleService implements IRoleService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Security/UserManagement/Role';
  }

  public Get(request: GetRoleRequest): Observable<GetRoleResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetRoleResponse>(apiUrl, { params: request as any });
  }

  public Submit(request: PostRoleRequest): Observable<PostRoleResponse>
  {
    const apiUrl = this.serviceUrl + '/Submit';
    return this.http.post<PostRoleResponse>(
      apiUrl, request,
      { headers: { 'Content-Type': 'application/json' } });
  }

  public SubmitDelete(request: DeleteRoleRequest): Observable<DeleteRoleResponse>
  {
    const apiUrl = this.serviceUrl + '/Delete';
    return this.http.post<DeleteRoleResponse>(
      apiUrl, request,
      { headers: { 'Content-Type': 'application/json' } });
  }

  public Update(request: UpdateRoleRequest): Observable<UpdateRoleResponse>
  {
    const apiUrl = this.serviceUrl + '/Update';
    return this.http.post(
      apiUrl, request,
      { headers: { 'Content-Type': 'application/json' } });
  }
}
