import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatSortModule } from '@angular/material/sort';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { RouterModule } from '@angular/router';
import { ConfirmDialogModule, SnackBarModule } from '@tei/material';
import { SettingsCardLayoutModule } from '@tei/theme';
import { UserService } from '../../@services/user.service';
import { RolePermissionModule } from '../role-permission/role-permission.module';
import { DeleteRoleDialogComponent } from './delete-role-dialog/delete-role-dialog.component';
import { RoleDialogComponent } from './role-dialog/role-dialog.component';
import { UserRoleDialogComponent } from './user-role-dialog/user-role-dialog.component';
import { Safe, UserComponent } from './user.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatTableModule, MatFormFieldModule, MatInputModule, MatIconModule, MatButtonModule,
  MatMenuModule, MatPaginatorModule, MatSortModule, MatTabsModule, MatSelectModule, DragDropModule];
const TEI_MATERIAL_MODULES = [SettingsCardLayoutModule, SnackBarModule, ConfirmDialogModule, RolePermissionModule];

@NgModule({
  declarations: [UserComponent, Safe, RoleDialogComponent, DeleteRoleDialogComponent, UserRoleDialogComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: UserComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [{ provide: 'IUserService', useClass: UserService }],
  exports: [],
})
export class UserModule { }
