import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { SnackBarService } from '@tei/material';
import { DeleteRoleRequest } from '../../../@intermediate/role/models/delete-role.model';
import { RoleService } from '../../../@services/role.service';

@Component({
  selector: 'tei-user-management-delete-role-dialog',
  templateUrl: './delete-role-dialog.component.html',
  styleUrls: ['./delete-role-dialog.component.css']
})
export class DeleteRoleDialogComponent implements OnInit
{
  model: DeleteRoleRequest = { RoleID: null };

  constructor(
    public dialogRef: MatDialogRef<DeleteRoleDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DeleteRoleRequest,
    public roleService: RoleService,
    private snackbarService: SnackBarService
  )
  {
    this.model = data;
  }

  ngOnInit(): void
  {
  }

  onDiscard(): void
  {
    this.dialogRef.close();
  }

  onDelete()
  {
    this.roleService.SubmitDelete(this.model).subscribe(
      () =>
      {
        this.onDiscard();
        this.snackbarService.showSuccess('Role deleted.');
      },
      err =>
      {
        let message: string = err.error;

        this.onDiscard();
        this.snackbarService.showError(message);
      });
  }
}
