import { Component, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { TranslateService } from '@ngx-translate/core';
import { concat, Observable, of, Subject } from 'rxjs';
import { catchError, debounceTime, delay, switchMap } from 'rxjs/operators';
import { FlattenStaff, Staff } from '../../@intermediate/group-staff/models/get-group-admin-staff.model';
// import { IGroupAdminStaffService } from '../../../@intermediate/group-admin-staff.interface';
import { GroupAdminStaffService } from '../../@services/group-admin-staff.service';
import { EditUserComponent } from './edit-user/edit-user.component';
import { InviteUsersComponent } from './invite-users/invite-users.component';

@Component({
  selector: 'tei-group-staff',
  templateUrl: './group-staff.component.html',
  styleUrls: ['./group-staff.component.scss']
})
export class GroupStaffComponent implements OnInit
{

  chipItems: string[] = ['DisplayName', 'Email', 'Status'];
  isLoading: boolean = false;
  chipItemsWithDisplayName = [
    { chipName: 'DisplayName', displayName: this.translate.instant('USER_MANAGEMENT.GROUP_STAFF.FULL_NAME') },
    { chipName: 'Email', displayName: this.translate.instant('USER_MANAGEMENT.GROUP_STAFF.EMAIL') },
    { chipName: 'Status', displayName: this.translate.instant('USER_MANAGEMENT.GROUP_STAFF.STATUS') }
  ];
  quickSearchValue = {};
  items: Observable<any[]>;
  contactArray: Observable<any[]> = of([]);
  contactTypeaheadInput$ = new Array();
  multiple: boolean;
  searchItem: string = '';
  flattenStaffs: FlattenStaff[];
  filteredFlattenStaff: FlattenStaff[];
  displayedColumns: string[] = ['User', 'Status', 'Actions', 'DotActions'];
  activeChipNameForSearch: string = "";
  selectedChipValues: { [key: string]: any; } = {
    'DisplayName': '',
    'Email': '',
    'Status': ''
  };

  constructor(
    // @Inject('IGroupAdminStaffService') private groupAdminStaffService: IGroupAdminStaffService
    private groupAdminStaffService: GroupAdminStaffService,
    private dialog: MatDialog,
    private translate: TranslateService
  )
  {
  }

  ngOnInit()
  {
    this.chipItems.forEach(el =>
    {
      this.contactTypeaheadInput$.push(new Subject<string>());
    });
    this.isLoading = true;
    this.groupAdminStaffService.Get().subscribe(res =>
    {
      this.flattenStaffs = this.getFlattenStaffs(res.StaffInfos);
      this.filterFlattenStaff(this.selectedChipValues);
      this.isLoading = false;
    });

  }

  getFlattenStaffs(staffs: Staff[]): FlattenStaff[]
  {

    return staffs.map(item =>
    {
      const { FirstName, LastName, Status, StatusDate, Date, Email, Picture } = item;
      const DisplayName = FirstName + ' ' + LastName;
      return { DisplayName, Status, StatusDate, Date, Email, Picture };
    });
  }

  filterByChipItem(item: { chipName: string, index: number; })
  {
    this.contactArray[item.index] = concat(
      of([]), // default items
      this.contactTypeaheadInput$[item.index].pipe(
        debounceTime(1000),
        switchMap((searchTerm) =>
          this.getStaff(searchTerm as string, item.chipName).pipe(
            catchError(() => of([])),
          ))
      )
    );
  }

  chipItemValues(selectedValues: string | string[])
  {
    this.selectedChipValues[this.activeChipNameForSearch] = selectedValues;
    this.filterFlattenStaff(this.selectedChipValues);
  }
  chipItem(event: { chipName: string, index: number; })
  {
    this.activeChipNameForSearch = event.chipName;
  }
  removeItem(event: { chipName: string; })
  {
    this.selectedChipValues[event.chipName] = '';
    this.filterFlattenStaff(this.selectedChipValues);
  }

  filterFlattenStaff(selectedChipValues: { [key: string]: any; })
  {
    this.filteredFlattenStaff = this.flattenStaffs.filter(item =>
    {

      for (let key in selectedChipValues)
      {
        if (selectedChipValues[key] && item[key] !== selectedChipValues[key])
          return false;
      }

      return true;
    });
  }


  getStaff(term: string = null, item: string): Observable<FlattenStaff[]>
  {
    let items = this.flattenStaffs;
    if (term)
    {
      const lowerTerm = term.toLocaleLowerCase();
      items = items.filter(x =>
        x[item].toLocaleLowerCase().indexOf(lowerTerm) !== -1
      );
    }
    return of(items).pipe(delay(200));
  }


  inviteUser(): void
  {
    this.dialog.open(InviteUsersComponent, {
      panelClass: 'invite-users-dialog',
      width: '500px'
    });
  }
  editUser(user: FlattenStaff): void
  {
    const dialogRef = this.dialog.open(EditUserComponent, {
      panelClass: 'invite-users-dialog',
      width: '500px',
      data: user
    });

    dialogRef.afterClosed().subscribe(res =>
    {
      if (res)
      {

      }
    });
  }
  deleteUser(user: FlattenStaff): void
  {

  }
}

