import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { DeleteRolePermissionRequest, DeleteRolePermissionResponse, GetRolePermissionRequest, GetRolePermissionResponse, IUMRolePermissionService, PostRolePermissionRequest, PostRolePermissionResponse } from '../@intermediate/role-permission';

@Injectable({
  providedIn: 'root'
})

export class RolePermissionService implements IUMRolePermissionService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Security/UserManagement/RolePermission';
  }

  public Get(request: GetRolePermissionRequest): Observable<GetRolePermissionResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetRolePermissionResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostRolePermissionRequest): Observable<PostRolePermissionResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostRolePermissionResponse>(apiUrl, request);
  }

  public Delete(request: DeleteRolePermissionRequest): Observable<DeleteRolePermissionResponse>
  {
    const apiUrl = this.serviceUrl + '/Delete';
    return this.http.post<DeleteRolePermissionResponse>(apiUrl, request);
  }

}
