import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetUserRequest, GetUserResponse, IUserService, PostUserRequest, PostUserResponse } from '../@intermediate/user';
import { PostUserRoleRequest, PostUserRoleResponse } from '../@intermediate/user/models/post-user-role.model';
import { UserResetPasswordRequest, UserResetPasswordResponse } from '../@intermediate/user/models/user-reset-password.model';

@Injectable({
  providedIn: 'root'
})

export class UserService implements IUserService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Security/UserManagement/User';
  }

  public Get(request: GetUserRequest): Observable<GetUserResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetUserResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostUserRequest): Observable<PostUserResponse>
  {
    const apiUrl = this.serviceUrl + '/Update';
    return this.http.post(
      apiUrl, request,
      { headers: { 'Content-Type': 'application/json' } });
  }

  public PostUserRole(request: PostUserRoleRequest): Observable<PostUserRoleResponse>
  {
    const apiUrl = this.serviceUrl + '/PostUserRole';
    return this.http.post(
      apiUrl, request,
      { headers: { 'Content-Type': 'application/json' } });
  }

  public UserResetPassword(request: UserResetPasswordRequest): Observable<UserResetPasswordResponse>
  {
    const apiUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Auth/ForgetPassword';

    return this.http.post(
      apiUrl, request,
      { headers: { 'Content-Type': 'application/json' } }
    );
  }
}
