import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RolesModule } from '@tei/admin-admin-center';
import { RolesWrapperComponent } from './roles-wrapper.component';

const BASE_MODULES = [CommonModule];
const COMPONENT = [RolesWrapperComponent];
const TEE_ADMIN_CENTER_MODULES = [
  RolesModule
];

@NgModule({
  declarations: [
    ...COMPONENT
  ],
  imports: [
    ...BASE_MODULES,
    ...TEE_ADMIN_CENTER_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...COMPONENT
  ]
})
export class RolesWrapperModule { }
