import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { UntypedFormGroup, FormGroupDirective, Validators } from '@angular/forms';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatSort } from '@angular/material/sort';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { ActivatedRoute } from '@angular/router';
import { SnackBarService } from '@tei/material';
import { GetCreateuserRequest, GetCreateuserResponse, ICreateuserService, PostCreateuserRequestForm, UserRoles, UserRolesMetadata } from '../../@intermediate/createuser';
@Component({
  selector: 'tei-user-management-pages-createuser',
  templateUrl: './createuser.component.html',
  styleUrls: ['./createuser.component.scss']
})
export class CreateuserComponent implements OnInit
{
  userName: string;
  model: GetCreateuserResponse;
  form: PostCreateuserRequestForm;
  dataSource: MatTableDataSource<UserRoles>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  metadata = new UserRolesMetadata();
  memberbackgroundcolor: string;
  publicbackgroundcolor: string;
  username_readonly: boolean;
  username_value: boolean = true;
  selectedRoles: string[] = [];
  formErrors = {
    'FirstName': '',
    'LastName': '',
    'Email': '',
    'Username': '',
    'Phone': '',
    'PasswordGroup': '',
    'Password': '',
    'ConfirmPassword': ''
  };
  validationMessages = {
    'FirstName': {
      'required': 'Full Name is required.',
    },
    'LastName': {
      'required': 'Last Name is required.',
    },
    'Email': {
      'required': 'Email is required.',
      'pattern': 'Please enter valid Email address'
    },
    'Username': {
      'required': 'Username is required.',
      'existingUser': 'Username already exists',
      'existingDeletedUser': 'Username exists in deleted users'
    },
    'Phone': {
      'required': 'Phone Number is required.',
      'pattern': 'Please enter valid Phone Number '
    },
    'Password': {
      'required': 'Password is required.',
      'minlength': 'Password must be greater than 6 characters',
    },
    'ConfirmPassword': {
      'required': 'Confirm Password is required.',
      'minlength': 'Password must be greater than 6 characters',
      'passwordMismatch': 'Passwords do not match.'
    },
    // 'PasswordGroup': {
    //   'passwordMismatch': 'Passwords do not match.'
    // },
  };

  constructor(@Inject('ICreateuserService') private CreateuserService: ICreateuserService,
    private snackbarService: SnackBarService, private activatedRoute: ActivatedRoute)
  {
    activatedRoute.params.subscribe(params =>
    {
      if (params['userName'] && params['userName'] !== '')
      {
        this.userName = params['userName'];
      }
    });
  }

  ngOnInit()
  {
    this.form = new PostCreateuserRequestForm();
    const request = new GetCreateuserRequest();
    this.publicbackgroundcolor = "selected-card";
    this.username_readonly = true;

    this.form.valueChanges.subscribe((data) =>
    {
      this.logValidationErrors(this.form);
    });

    this.form.get('Email').valueChanges.subscribe((data) =>
    {
      if (this.username_value)
      {
        this.form.get('Username').patchValue(this.form.get('Email').value);
      }
    });

    this.load(request);
  }

  logValidationErrors(group: UntypedFormGroup = this.form): void
  {
    Object.keys(group.controls).forEach((key: string) =>
    {
      const abstractControl = group.get(key);
      this.formErrors[key] = '';
      if (abstractControl && !abstractControl.valid && (abstractControl.touched || abstractControl.dirty || key == 'Username'))
      {
        const messages = this.validationMessages[key];
        for (const errorKey in abstractControl.errors)
        {
          if (errorKey)
          {
            this.formErrors[key] += messages[errorKey] + ' ';
          }
        }
      }
      if (abstractControl instanceof UntypedFormGroup)
      {
        this.logValidationErrors(abstractControl);
      }
    }
    );
  };

  onUserNameSetValidity()
  {
    const usernameFormControl = this.form.get('Username');
    usernameFormControl.setValidators(Validators.required);
    usernameFormControl.updateValueAndValidity();
  }
  onUserNameclearValidity()
  {
    const usernameFormControl = this.form.get('Username');
    usernameFormControl.clearValidators();
    usernameFormControl.updateValueAndValidity();
  }

  private load(request: GetCreateuserRequest)
  {
    if (!this.model)
    {
      this.model = new GetCreateuserResponse();
    }

    request.UserName = this.userName;
    request.UserType = this.setMemberType(this.username_readonly);
    this.CreateuserService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.form.patchValue(response);

      this.bindCreateUsers(this.model.DisplayUserRole);
    });
  }

  private bindCreateUsers(displayuserrole: UserRoles[])
  {
    this.dataSource = new MatTableDataSource<UserRoles>(displayuserrole);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  OnMemberClickEvent()
  {
    this.username_readonly = false;
    this.publicbackgroundcolor = "";
    this.memberbackgroundcolor = "selected-card";
    this.form.get('Username').reset();
    this.username_value = false;
    this.onUserNameSetValidity();

    //reload the roles based on new user type
    this.load(new GetCreateuserRequest());
  }

  OnPublicUserClickEvent()
  {
    this.username_readonly = true;
    this.memberbackgroundcolor = "";
    this.publicbackgroundcolor = "selected-card";
    this.form.get('Username').patchValue(this.form.get('Email').value);
    this.username_value = true;
    this.onUserNameclearValidity();

    //reload the roles based on new user type
    this.load(new GetCreateuserRequest());
  }

  chkSingleChange($event, row?: UserRoles)
  {
    const checked = $event.checked;
    if (checked)
    {
      this.model.DisplayUserRole.find(x => x.RoleName == row.RoleName).Checked = true;
      this.selectedRoles.push(row.RoleName);
      this.form.get("Roles").patchValue(this.selectedRoles);
    }
    else
    {
      this.model.DisplayUserRole.find(x => x.RoleName == row.RoleName).Checked = false;
      let selectedRoleNames = this.selectedRoles.filter(x => x != row.RoleName);
      this.selectedRoles = [];
      selectedRoleNames.forEach(x =>
      {
        this.selectedRoles.push(x);
      });
      this.form.get("Roles").patchValue(selectedRoleNames);
    }

  }

  save(form: PostCreateuserRequestForm, formDirective: FormGroupDirective)
  {
    this.CreateuserService.Post(form.value).subscribe(response =>
    {
      if (response.ExistingUser)
      {
        this.snackbarService.showSuccess('Username Already Exists.');
      }
      else
      {
        this.snackbarService.showSuccess('User Successfully Created.');
      }

      window.scroll(0, 0);
      formDirective.resetForm();
      form.reset();
      this.model.DisplayUserRole.forEach(x =>
      {
        x.Checked = false;
      });
      form.get("Roles").reset();
    }, error =>
    {
      if (error.error.Message == "Username already exists")
      {
        const Username = form.get('Username');
        Username.setErrors({ 'existingUser': true });
        this.logValidationErrors(this.form);
        this.snackbarService.showError(error.error.Message);
      }
      else if (error.error.Message == "Username exists in deleted users")
      {
        const Username = form.get('Username');
        Username.setErrors({ 'existingDeletedUser': true });
        this.logValidationErrors(this.form);
        this.snackbarService.showError(error.error.Message);
      } else
      {
        this.snackbarService.showError(error.error);
      }
    });
  }

  setMemberType(username_readonly: boolean): string
  {
    if (username_readonly)
    {
      return "Public";
    }

    return "Member";
  }
}


